*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
.ipop{
  width: 46.5%;
  height: 50px;
  margin: 5% 0% 0% 40%;
  border-radius: 5px;
  border: 1px solid #999999;
  outline: none;
  font-weight: bold;
  font-size: 40px;
  text-align: right;
}
.ipop2{
  width: 46.5%;
  height: 50px;
  margin: 5% 0% 0% 10%;
  border-radius: 5px;
  border: 1px solid #999999;
  outline: none;
  font-weight: bold;
  font-size: 40px;
  text-align: right;
}
button{
  width: 80px;
  height: 80px;
  font-weight: bold;
  font-size: 40px;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid #999999;
  outline: none;
}
button:hover{
  background-color: #0077b5;
  color: #fff;
}
.calc1{
  margin: 0% 0% 0% 40%;
}
.calc2{
  margin: 0% 0% 0% 10%;
}
.eval{
  margin: 5% 0% 0% 49%;
  font-weight: bold;
   text-transform: uppercase;
   margin-top: 5%;
}
.weval{
  margin: 5% 0% 0% 17%;
  font-weight: bold;
   text-transform: uppercase;
   margin-top: 5%;
}
.row{
  display: flex;
}
.left{
  float: left;
  width: 50%;
}
.right{
  float: right;
  width: 50%;
}
.btncolor{
  background-color: #f73f00;
  color: #fff;
}
.btnequal{
  background-color: green;
  color: #fff;
}
.scibtn{
  font-weight: normal;
  font-size: 20px;
}
.scidiv{
  position: absolute;
  top: 34%;
  right: 4%;
}
i{
  color: #f73f00;
}
button:hover i{
  color: #fff;
}
.scibtnn{
  font-weight: normal;
  font-size: 17px;
}